.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282934;
  padding-top: 1em;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.titulo{
  font-size: x-large;
}

.App-link {
  color: #61fb69;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mapa{
  width: 80%;
  margin: auto;
}

.social-icon:hover{
  transform: rotateZ(20deg);
}

.whatsApp-icon{
  padding: 0em;
  margin-left: 0em;
  margin-right: 0.5em;
}